<!---->
<template>
  <div>
    <div class="choosePerson">
      <!-- -->
      <div  class="choose-person-container">
        <div class="content-body">
          <el-row>
            <!--  begin -->
            <div class="choosePerson-data">

              <!--  begin -->
              <div class="search-condition btn-linkage">
                <el-row>
                  <el-col :span="24" class="serchCOL">
                    <el-input v-model="searchTxt"
                              placeholder=""
                              clearable
                              @keyup.enter.native="handleSearch"
                              class="searchText">
                    </el-input>
                    <div class="searchline">
                      <el-button type="primary"
                                 icon="el-icon-search"
                                 @click="handleSearch"
                                 class="choosenBtn">
                      </el-button>
                      <template>
                        <el-checkbox v-model="LeaveDimission" class="choosenSwitch"></el-checkbox>
                      </template>
                    </div>
                  </el-col>
                </el-row>
              </div>
              <!--  end -->

              <el-col :span="24">
                <!--<el-scrollbar class="scroll-bar">-->
                <!--  begin -->
                <div class="table-box">
                  <el-table
                    ref="employeeTable"
                    :highlight-current-row="true"
                    :data="orgPersonTable"
                    :height="personTableHeight"
                    border
                    empty-text=""
                    tooltip-effect="dark"
                    style="width: 100%;text-align: center"
                    @select-all="selectAll"
                    @select="selectCheckChange">
                    <el-table-column
                      type="selection"
                      width="45"/>
                    <el-table-column
                      label=""
                      center
                      prop="UserName"
                      show-overflow-tooltip
                      width="100">
                    </el-table-column>
                    <el-table-column
                      label=""
                      prop="UserAccount"
                      show-overflow-tooltip/>
                    <el-table-column
                      label=""
                      width="70"
                      show-overflow-tooltip>
                      <template slot-scope="Scope">
                        <span>{{isLeave}}</span>
                      </template>
                    </el-table-column>
                    <el-table-column
                      label=""
                      width="50"
                      show-overflow-tooltip style="text-align: center">
                      <template slot-scope="Scope">
                        <i class="icon iconfont" :class="Scope.row.IsCollect?'icon-noCollect-icon':'icon-collect-icon-copy'" :title="Scope.row.IsCollect?'':''"
                           @click.stop="Collectchange(Scope.row)" style="font-size:16px"></i>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
                <!--</el-scrollbar>-->
              </el-col>

            </div>
            <!--  end -->
          </el-row>
        </div>
      </div>
      <!-- -->
      <!-- tab end -->
    </div>
    <!--  begin -->
    <div class="table-foot">
      <div class="page-sizes-box">
        <page :pageIndex="infoPage.pageIndex"
              :total="infoPage.total"
              :pageSize="infoPage.pageSize"
              :pager-count="4"
              :pageSizesList="infoPage.pageSizesList"
              @handleSizeChange="handleSizeChange"
              @handleCurrentChange="handleCurrentChange">
        </page>
      </div>
    </div>
    <!--  end -->
  </div>
</template>
<script>
  import page from '@/plugins/page';
  import { baseUrl } from '../../../static/commonApi';
  //
  export default {
    name: 'choosenTable',
    data () {
      return {
        loading: true,
        //
        infoPage:{
          pageIndex: 1,
          pageSize: 10,
          currentPage:1,
          total:0,
          pageSizesList: [10, 20, 50],
        },
        deletechooseheight:'deletechooseheight',
        deletechoosecontent:'deletechoose-content',
        // 
        // collection:true,
        // tab
        activeName:'first',
        tabtoggle:0,
        //Id
        currentSelectOrganizationId: '',
        // 
        tableData: [],
        //
        currentRow:null,
        //
        usertags:[],
        // 
        isActive:false,
        //
        searchTxt:'',
        // 
        orgPersons: [],
        newisEmptyApplicant:false,
        LeaveDimission:false,//
        multipleSelection: [],
        isLeave:'', //
        orgPersonTable:[],//
        collectIcon:'',
        dialogTags:[],//
        userPickUrl:baseUrl, //Url
        tempOrgID:'',
      };
    },
    components: {
      page
    },
    props: {
      persons: {
        type: String,
        default: ''
      },
      chooseDatas: {
        type: Object,
        default: () => {
          return {
            row: null,
            name: '',
            id: ''
          };
        }
      },
      deleteTags:{
        type:Object,
        default:()=>{
          return {}
        }
      },
      // 
      tags:{
        type:Array,
        default: []
      },
      type:{
        type:String,
        default:''
      },
      mutilselectCheck:{
        type:String,
        default:''
      },
      currentOrgId:{
        type:String,
        default:''
      },
      parmDialog:{
        type:Object,
        default: ()=>{
          return new Object()
        }
      }
    },
    computed: {
      personTableHeight() {
        return this.tableData.length > 0 ? 195 : 240;
      },
      userTableHeight() {
        return this.userdata.length > 0 ? 195 : 230;
      }
    },
    watch: {
      //
      tags:{
        //
        handler: function (newVal,oldVal) {
          if(newVal){
            if(this.parmDialog.classifier){
              this.setIframeSelect(this.orgPersonTable,newVal)
            }else {
              this.doSelection(this.orgPersonTable,newVal);
            }
            this.dialogTags = [...newVal];
          }
        },
        deep:true,
      },
      //
      deleteTags:{
        //
        handler: function (newVal,oldVal) {
//          if(this.parmDialog.classifier){
//            debugger
//            this.setIframeSelect(this.orgPersonTable,[newVal])
//          }
          this.onlyDelete(newVal)
        },
        deep:true,
      },
    },
    mounted() {
      var _this = this;
      _this.ownIsleave(_this.orgPersonTable);
      _this.dialogTags = [..._this.tags];
    },
    methods: {
      // 
      selectCheckChange (selection, row) {
        row.type = 'User';
        let _this = this;
        //
        if(_this.mutilselectCheck==='true'){
          for(let item of selection){
              item.type = 'User';
              item.UserAccount = item.UserAccount;
              item.Name = item.UserDisplayName;
            }
          let onlyUserAccount =  _this.getPersonAccount(_this.orgPersonTable);
          //
          _this.dialogTags = _this.dialogTags.filter(item => onlyUserAccount.indexOf(item.UserAccount) == -1);
          for (let item of selection) {
           _this.dialogTags.push(Object.assign({}, item));
          }
        }
        //
        else {
          //
          if(selection.length>=1){
            //
           let arrA  = [..._this.dialogTags];
           //
            if(_this.dialogTags && _this.dialogTags.length>=1){
              for (let item of arrA){
                //
                if(item.type==='User'){
                  let temp  = selection.filter(item=>item.UserAccount !==row.UserAccount);
                  _this.$refs.employeeTable.toggleRowSelection(temp[0],false);
                  let dataIndex = _this.dialogTags.findIndex(item=>item.type==='User');
                  _this.dialogTags.splice(dataIndex,1,row)
                }
                //push
                else if(!_this.dialogTags.includes(row.type) && selection.length===1) {
                  _this.dialogTags.push(row);
                }
              }
            }else {
              _this.dialogTags.push(row)
            }
          }
//          //
          else {
            _this.$refs.employeeTable.toggleRowSelection(row,false);
            let delIndex =  _this.dialogTags.findIndex(item=>item.SysId===row.SysId);
            _this.dialogTags.splice(delIndex,1);
          }
        }
        //tags
        _this.$emit('choosePersonTag',_this.dialogTags);
      },
      // Id
      getPersonEmpId(list){
        let empIds = [];
        for(let item of list){
          let sysid=item.SysId;
          if(sysid==undefined){
            empIds.push(item.SysId);
          }
          else{
            empIds.push(item.SysId);
          }

        }
        return empIds;
      },
      // 
      handleSizeChange(val) {
        var that = this;
        var pageSize =  val;
        that.infoPage.pageIndex = 1;
        that.infoPage.pageSize = parseInt(pageSize);
        if(that.searchTxt){
          that.handleSearch()
        }
        else{
          that.getPerson(that.searchTxt,that.currentOrgId,that.LeaveDimission);
        }
      },
      //
      handleCurrentChange(val) {
        let _this = this;
        _this.infoPage.pageIndex = val;
        if(_this.searchTxt){
          _this.handleSearch()
        }
        else{
          _this.getPerson(_this.searchTxt,_this.currentOrgId,_this.LeaveDimission);
        }
        setTimeout(function () {
          _this.doSelection(_this.orgPersonTable,_this.dialogTags);
          _this.setIframeSelect(_this.orgPersonTable,_this.dialogTags);
        },500)
      },
      //
      setToggleSelect(rows){
        var that = this;
        rows.forEach(rows => {
          that.$refs.employeeTable.toggleRowSelection(rows,true);
        });
      },
      // 
      selectAll(val){
        var _this = this;
        //
        if(val.length===0){
          //
          _this.dialogTags.splice(0,_this.dialogTags.length)
        }
        //
        if(_this.mutilselectCheck==='false'){
          _this.$nextTick(() => {
            _this.$refs.employeeTable.clearSelection();
          });
          for(let v of val){
            if(v.SysId){
              _this.dialogTags = []
            }else {
              _this.dialogTags = _this.dialogTags.filter((item)=>{
                return item.SysId !== v.SysId;
              });
            }
          }
        }
        //
        else {
          let userAccount = _this.getPersonAccount(_this.dialogTags);
          for (let item of val){
            item.type = 'User';
            if(userAccount.includes(item.UserAccount)){

            }else {
              _this.dialogTags.push(item)
            }
          }
        }

        for(let item of _this.dialogTags){
          switch (item.type){
            case 'User':{
              item.UserAccount = item.UserAccount;
              item.Name = item.UserDisplayName;
            }
          }
        }
        _this.$emit('choosePersonTag',_this.dialogTags)
      },
      //
      doSelection(val,list) {
        var _this = this;
        _this.$nextTick(() => {
          if (list && list.length>=1) {
            if(_this.mutilselectCheck==='false'){
              _this.$refs.employeeTable.clearSelection();
            }
            list.forEach((item) => {
                const index = val.findIndex((v) => v.UserAccount === item.UserAccount);
              if(index>-1){
                _this.$nextTick(() => {
                  _this.$refs.employeeTable.toggleRowSelection(val[index], true);
                })
              }else {
                _this.$nextTick(() => {
                  _this.$refs.employeeTable.toggleRowSelection(val[index], false);
                })
              }
            });
          }
          else if(list.length === 0){
            _this.$nextTick(() => {
              _this.$refs.employeeTable.clearSelection();
            })
          }
        });
      },
      //id
      getPersonId(list){
        var onlySid = [];
        for(let item of list){
          let ssid = item.SysId;
          if(ssid){
            onlySid.push(ssid)
          }
        }
        return onlySid
      },
    //
      getPersonAccount(list){
        var onlySid = [];
        for(let item of list){
          let ssid = item.UserAccount;
          if(ssid){
            onlySid.push(ssid)
          }
        }
        return onlySid
      },
      //
      onlyDelete(oldValue) {
        var _this = this;
        let delindex = _this.getPersonAccount(_this.orgPersonTable);
        const index = delindex.findIndex(item=>item===oldValue.UserAccount);
        _this.$refs.employeeTable.toggleRowSelection(_this.orgPersonTable[index], false);
      },
      //
      Collectchange(val){
        var that = this;
        if(!val.IsCollect){
          var data={
            IsCollect:true,
            refUserId:val.SysId
          };
          that.$axios({
            url: that.userPickUrl.Base_User_URL + '/SetCollectPerson',
            method:'POST',
            data:data
          }).then(res=>{
            if(res.data.code===0){
              that.$message.success('');
              val.IsCollect = !val.IsCollect
            }
            else {
              that.$message.success('');
            }
          }).catch(err=>{
            that.$message.error(`!ERROR MESSAGE:${err.message}`);
          })
        }
        else {
          var data={
            IsCollect:false,
            refUserId:val.SysId
          };
          that.$axios({
            url:that.userPickUrl.Base_User_URL +'/SetCollectPerson',
            method:'POST',
            data:data
          }).then(res=>{
            if(res.data.code===0){
              that.$message.success('');
              val.IsCollect = !val.IsCollect
            }
            else {
              that.$message.success('');
            }
          }).catch(err=>{
            that.$message.error(`!ERROR MESSAGE:${err.message}`);
          })
        }
      },
      //
      handleSearch(){
        //
        let that = this;
        if(that.searchTxt===''){
          that.$message.error("");
          return
        }
        that.getPerson(that.searchTxt,that.currentOrgId,that.LeaveDimission)
      },
      //
      ownIsleave(val){
        var that = this;
        for(let item of val){
          if(item.UserStatus){
            that.isLeave = ''
          }
          else {
            that.isLeave = ''
          }
        }
      },
      // 
      getPerson (val,OrgId,isLeave) {
        let _this = this;
        if(val!=='' && val){
          OrgId = ''
        }
        // 
        const data = {
          page: _this.infoPage.pageIndex,
          limit: _this.infoPage.pageSize,
          OrgId: OrgId,
          key:val,
          isLeave:isLeave
        };
        _this.doRest(data, function (result) {
          _this.orgPersonTable = result.data;
          _this.infoPage.total = result.count;
          _this.ownIsleave(_this.orgPersonTable);
          _this.doSelection(_this.orgPersonTable,_this.dialogTags);
            _this.setIframeSelect(_this.orgPersonTable,_this.dialogTags);
        },(err)=>{

        });
      },
      // api
      doRest(data, callback) {
        const me = this;
        me.loading = true;
        me.$axios.post(me.userPickUrl.Base_User_URL +'/GetUserListByOrgId',data).then(result => {
          if (result.status === 200) {
            callback(result.data);
            me.loading = false;
          }
          else {

          }
        })
          .catch(error => {
            me.loading = false;
            throw error;
          });
      },
      //httpService 
      httpService(params,coolback){
        let _this = this;
        _this.loading = true;
        _this.$axios(params).then(result => {
          _this.loading = false;
          coolback(result);
        }).catch(error => {
          _this.loading = false;
          throw error;
        });
      },
      //iframe
      setIframeSelect(val,list) {
        var _this = this;
        _this.$nextTick(() => {
          if (list && list.length>=1) {
            if(_this.mutilselectCheck==='false'){
              _this.$refs.employeeTable.clearSelection();
            }
            list.forEach((item) => {
              const index = val.findIndex((v) => v.UserAccount === item.UserAccount);
              if(index>-1){
                  _this.$refs.employeeTable.toggleRowSelection(val[index], true);
              }else {
                _this.$nextTick(() => {
                  _this.$refs.employeeTable.toggleRowSelection(val[index], false);
                })
              }
            });
          }
          else if(list.length === 0){
            _this.$nextTick(() => {
              _this.$refs.employeeTable.clearSelection();
            })
          }
        });
      },
    },
  };
</script>

<style lang="scss" scoped>
  .serchCOL{
    display: flex;
    justify-content: space-between;
    .searchText{
    }
    .searchNext{
      display: flex;
      justify-content: space-between;
    }
  }
  .icon-noCollect-icon{
    color: #FFB800;
  }
</style>



