<!---->
<template>
  <div>
    <el-tree
      ref="systemRoleTree"
      :node-key="nodKey"
      :props="defaultProps"
      :data="organizationList"
      :highlight-current="highlightCurrent"
      :check-on-click-node="checkOnClickNode"
      :expand-on-click-node="expandOnClickNode"
      :auto-expand-parent="autoExpandParent"
      :load="loadNodeData"
      :loading="loading"
      :height="personTableHeight"
      lazy
      @node-click="nodeClick"
      @current-change="currentChange"
      @check-change="checkChange"
      @check="check">
    </el-tree>
  </div>
</template>
<script>
// `debounce`  Lodash 
import {debounce} from 'lodash';
import { baseUrl } from '../../../static/commonApi';
export default {
  name: 'systemRoleTree',
  props: {
    // tree  key
    nodKey:{
      type:String,
      default:'id',
      require:false
    },
    //
    highlightCurrent:{
      require: false,
      type: Boolean,
      default: true
    },
    //
    checkOnClickNode:{
      type:Boolean,
      require:false,
      default:true
    },
    //
    expandOnClickNode:{
      type:Boolean,
      require:false,
      default:true
    },
    //
    renderexpand:{
      type:Boolean,
      require:false,
      default:true,
    },
    //
    autoExpandParent:{
      type:Boolean,
      require:false,
      default:true,
    },

    //  IdPath
    depthIdPath:{
      type:Boolean,
      require:false,
      default:false,
    },
    // 
    isMountedGetData:{
      type:Boolean,
      require:false,
      default:true,
    },
    // 
    defaultRootNode:{
      type:Object,
      require:false,
      default:null,
    },
    //  
    defaultPropLabel:{
      type:String,
      require:false,
      default:'displayName',
    },
    //  
    defaultSort:{
      type:String,
      require:false,
      default:'orderIndex',
    },

  },
  data () {
    return {
      organizationList:[],
      //
      // tree 
      defaultProps:{
        label:'title',
        children: 'children',
        //  
        isLeaf:'isLast',
      },
      // loadding
      loading:false,
      //  tree  node
      currentNode:null,
      //
      isFristLoadTree:true,
      userPickUrl:baseUrl //Url
    };
  },
  created: function (node) {
    var _this = this;
    // `debounce`  Lodash 
    _this.debouncedSearchTree = debounce(_this.getTreeList,null);
  },
  mounted() {
    if( this.isMountedGetData){
      this.debouncedSearchTree(null);
    }
  },
  methods: {
    /**
      * tree  
      * @param {Object} node tree Node
      * @param {Function} resolve  
      */
    loadNodeData(node, resolve) {
      if (node.level === 0) {
        this.isFristLoadTree = false;
        return;
      }
      else {
        this.debouncedSearchTree(node,resolve);
        return;
      }
    },
    /**
      * 
      * @param {String} code code
      * @param {String} aId  Id
      * @param {Function} resolve  
      * @param {String} currentKey  Id
      * @param {Boolean} first  
      */
    getTreeList(node,resolve){
      let _this = this;
      //
      if(node === null){
        var data = null
      }
      //
     else {
        if(!node.data.isLast) {
          var data = {
            ParentID: node.data.id,
            context: node.data.title,
          }
        }
      }
      let params = {
        url: '/System/UserPick/GetSystemRoleCategoryByParent',
        method: 'post',
        data:data
      };
      _this.httpService(params,(result)=>{
        if(result.data && result.status === 200) {
          if(result.data.data && result.data.data.length > 0){
            let data = result.data.data;
            if(resolve){
                resolve(data);
            }
            else{
              _this.organizationList = data;
            }
          }else{
            // 
            if(resolve){
              resolve([]);
            }
          }
        }
        _this.$emit('closeLoadding');
      });
    },
    /**
      *  tree 
      * @param {Object} data tree  data
      * @param {Object} node  tree  Node
      * @param {Element} e  
      */
    nodeClick(data,node,e){
      //
      this.currentNode = node;
      this.$emit('node-click',data,node,e);
    },
    /**
      * 
      * @param {Object} data tree  data
      * @param {Object} node  tree  Node
      */
    currentChange(data,node){
      this.$emit('current-change',data,node);
    },
    /**
      * 
      * @param {Object} data  tree  data
      * @param {Boolean} self 
      * @param {Element} self  tree 
      */
    checkChange(data,self,child){
      this.$emit('check-change',data,self,child);
    },
    /**
      * 
      * @param {Object} data tree  data
      * @param {Object} node  tree  Node
      */
    check(data,node){
      this.$emit('check',data,node);
    },
    /**
     *  key / data 
     * @param {String|Object} obj 
     * @param {Boolean} checked 
     * @param {Boolean} deep 
     */
    setChecked(obj,checked=false, deep=false){
      let _this = this;
      //
      if( _this.$refs.systemRoleTree){
        _this.$nextTick(()=>{
          _this.$refs.systemRoleTree.setChecked(obj,checked, deep);
        });
      }
    },
    /**
     *  key 
     * @param {String|Object} key 
     */
    setCurrentKey(key){
      let _this = this;
      _this.$nextTick(()=>{
        if(key && _this.$refs.systemRoleTree) {
          _this.$refs.systemRoleTree.setCurrentKey(key);
          let node = _this.$refs.systemRoleTree.getNode(key);
          if (node) {
            // event
            _this.$emit('node-click', node.data, node, _this.$refs.systemRoleTree.$el);
          }
        }
      });
    },
    /**
     *  keys 
     * @param {Array} keys  key 
     * @param {Boolean} leafOnly  true 
     */
    setCheckedKeys(keys, leafOnly=false){
      let _this = this;
      _this.$nextTick(()=>{
        this.$refs.systemRoleTree.setCheckedKeys(keys,leafOnly);
      });
    },
    getNode(id){
      return this.$refs.systemRoleTree.getNode(id);
    },
    getCheckedNodes(){
      return  this.$refs.systemRoleTree.getCheckedNodes();
    },
    /**
     * list to tree
     * @param {Array} data 
     * @returns Array
     */
    toTree(data) {
      if (data == null || data.length == 0) return [];
//      let list = [];
      let val = [];
      let map = {};
//      for (let item of data) {
//        let obj = Object.assign({}, item.parentId);
//        list.push(obj);
//      }
      data.forEach(function(item) {
        delete item.children;
      });
      data.forEach(function(item) {
        map[item.id] = item;
      });
      data.forEach(function(item) {
        var parent = map[item.parentId];
        if (parent) {
          (parent.children || (parent.children = [])).push(item);
        } else {
          val.push(item);
        }
      });
      return val;
    },
    //httpService 
    httpService(params,coolback){
      let _this = this;
      _this.loading = true;
      _this.$emit('openLoadding');
      _this.$axios(params).then(result => {
        _this.loading = false;
        coolback(result);
      }).catch(error => {
        _this.loading = false;
        _this.$emit('closeLoadding');
        throw error;
      });
    },

    //
    getSecode(data){
      var _this = this;
      var data={
        ParentID: data.data.id,
        parentId: -1,
        context:  data.data.title,
        isLeaf: data.data.isLast,
        level: data.level,
        spread: false,
      };
      let params = {
        url: _this.userPickUrl.Base_User_URL+'/GetChildOrgTree',
        method: 'post',
        data:data
      };
      if(!data.isLast){
        _this.$axios(params).then(result=>{

        })
      }
    }
  },
  computed: {
    personTableHeight() {
      return this.organizationList.length > 0 ? 185 : 223;
    },
  }
};
</script>




