<!--  -->
<template>
  <div class="originater_dialog newProcess_dialog">
    <div :class="[componentParam.closeShow?'pc-candidates':'pc-candidates pc-background']">
      <div class="dialog_content" >

        <el-main style="position:relative;">
          <div class="top" v-show="!componentParam.closeShow">
            <span style="font-size: 30px;cursor: pointer" @click="close"></span>
          </div>
          <el-tabs type="card"
               v-loading="imgLoading"
              element-loading-text="..."
              element-loading-background="rgba(255, 255, 255, 0.8)">
            <el-tab-pane class="candidates-recently">
              <span slot="label">
                <i class="iconfont icon-todos"></i>&nbsp;</span>
              <div class="title  title-border">
                <!--**  begin -->
                <el-breadcrumb separator-class="el-icon-arrow-right" :title="organizeProcessName + tooltiprocessName">
                  <el-breadcrumb-item></el-breadcrumb-item>
                  <el-breadcrumb-item style="overflow: hidden; -ms-text-overflow: ellipsis;text-overflow: ellipsis; white-space: nowrap; width: 300px; display: inline-block;">{{ organizeProcessName }}{{ processName }}</el-breadcrumb-item>
                </el-breadcrumb>
                <!--**  end -->
                <span class="rightInput">
                  <el-input placeholder="" prefix-icon="el-icon-search" v-model="treeSearch"  @keyup.enter.native="enterSearch" size="mini">
                  </el-input>
                </span>
              </div>
              <div class="content clearfix">
                <div style="position: relative;" class="content-left">
                  <div class="title">
                  <!--   -->
                    <el-form style="width: 100%;" :inline="true" class="demo-form-inline init-organize-box">
                      <el-form-item style="width: 100%;" label="">
                          <el-select style="width: 100%;" ref='selectTree' v-model="organizeName" placeholder="" popper-class="tree-list-box">
                            <el-option  style="height:auto;padding:0px 0px;" value="" :disabled = true>
                              <div>
                                <el-tree
                                :data="organizeList"
                                :highlight-current="true"
                                node-key='orgID'
                                default-expand-all
                                :expand-on-click-node = false
                                empty-text=''
                                :props="props"
                                @node-click="selectTreeClick"
                                ref="orgtree"></el-tree>
                              </div>
                            </el-option>
                          </el-select>
                      </el-form-item>
                    </el-form>
                  </div>

                  <el-tree v-if="treeList.length > 0" :data="treeList" node-key="id"
                    :highlight-current="true"
                    :props="defaultProps"
                    empty-text=''
                    :expand-on-click-node = false
                    :default-expanded-keys = defaultKeys
                    @node-click="handleNodeClick"
                    ref="processTree">
                  </el-tree>
                  <div style="position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%);-moz-transform: translate(-50%, -50%);-ms-transform: translate(-50%, -50%);-o-transform: translate(-50%, -50%);transform: translate(-50%, -50%);" v-if="treeList.length === 0">
                    
                  </div>

                </div>
                <div class="content-right" style="position: relative;">
                  <!--   -->
                  <el-checkbox v-if="!componentParam.oneCheck" :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange" style="padding-top:6px;padding-left:20px;">
                  </el-checkbox>
                  <el-checkbox-group class="theRadio" v-if="componentParam.oneCheck" v-model="checkedCities">
                    <el-checkbox-button v-for="item in cities" :label="item" :key="item.id" :checked="item.checked">
                      <div class="media">
                        <div class="media-body" @click="clickDepartment(item)" @dblclick="dblclickCheckedCitie(item)">
                          <h4 class="media-heading iconfont icon-process1" :title="item.name">{{(item.name.length>30)?item.name.substr(0,30)+"...":item.name}}</h4>
                        </div>
                      </div>
                    </el-checkbox-button>
                  </el-checkbox-group>
                  <!--   -->
                  <el-checkbox-group class="mut-check" v-else v-model="checkedCities" @change="handleCheckedCitiesChange">
                    <el-checkbox v-for="item in cities" :label="item" :key="item.id" :checked="item.checked">
                      <div class="media">
                        <div class="media-body">
                          <h4 class="media-heading" :title="item.name">{{(item.name.length>30)?item.name.substr(0,30)+"...":item.name}}</h4>
                        </div>
                      </div>
                    </el-checkbox>
                  </el-checkbox-group>
                  <!--  -->
                  <div style="position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%);-moz-transform: translate(-50%, -50%);-ms-transform: translate(-50%, -50%);-o-transform: translate(-50%, -50%);transform: translate(-50%, -50%);" v-if="cities.length === 0">
                    
                  </div>
                </div>
                <!--   -->
              </div>
              <div class="addrecently-box" v-bind:class="{ pullup: isActive }">
                <div class="addrecently">
                  <div class="media">
                    <div class="pull" @click="pullBtnHeightChange"></div>
                    <ul class="clearfix">
                      <!--  -->
                      <div class="clearBtn" v-show="selectedCities.length>0" @click="clearDept"></div>
                      <li v-for="(project,index) in selectedCities" :key="index">
                        <div class="media-body">
                          <i class="el-icon-circle-close" @click="deleteDept(index)"></i>
                          <span class="media-heading">{{project.name}}</span>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="addrecently-footer">
                <el-button type="primary" @click="sureSubmit" icon="el-icon-circle-check-outline" size="mini">
                </el-button>
                <el-button type="primary" @click="close" icon="el-icon-circle-close-outline" size="mini"></el-button>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-main>
      </div>
    </div>
  </div>
</template>

<script>
// `debounce`  Lodash 
import {debounce} from "lodash";
import util from "../../js/util";

export default {
  name: "",
  props: {
    //
    componentParam: {
      //
      componentType: {
        type: String,
        required: true
      },
      //api(key)
      childDataUrl: {
        type: Object,
        required: true
      },
      //  
      backfillData: {
        type: Array,
        default:new Array(),
        required: true
      },
      //
      assemblyParam: {
        type: Object,
        required: true
      },
      //(true:false:)
      checkProcess: {
        type: Boolean,
        default: false,
        required: true
      },
      //(truefalse)
      oneCheck: {
        type: Boolean,
        default: false,
        required: true
      },
      //()
      closeShow: {
        type: Boolean,
        default: true,
        required: true
      },
      type: Object,
      required: true
    }
  },
  mounted() {
    if ( this.componentParam.backfillData.length>0) {
      for (let item of this.componentParam.backfillData) {
        this.selectedCities.push(item);
      }
    }
  },
  data() {
    return {
      checkedCities: [], //
      selectedCities: [], //
      processName: "", //
      organizeProcessName: "", //  
      organName: "", //
      selectOrgs: [], //
      isActive: false, //
      checkAll: false,
      cities: [], //
      treeSearch: "", // 
      treeList: [], //
      rootNode: {},//
      //tree 
      defaultProps: {
        id: "id",
        children: "Child",
        label: "name"
      },
      tooltiprocessName: "", //tip
      defaultKeys: [], //treekey
      currentId: "", //orgId
      isIndeterminate: false, //checkbox
      // tree
      props: {
        label:'orgName',
        value: 'orgID',
        children: 'm_ChildOrg'
      },
      organizeList: [],//
      activeOrganizeName:[],//
      organizeName:'',//
      //
      organize:{orgID:''},
      imgLoading:false
    };
  },
  watch: {
    // 
    treeSearch(val) {
      var that = this;
      that.debouncedSearchTree();
    }
  },
  created: function (param) {
    let that = this;
    // `debounce`  Lodash 
    that.debouncedSearchTree = debounce(that.searchTree, 2000);
    //tree  
    that.debouncedGetProcess = debounce(that.getProcess, 200,param);
    that.imgLoading = true
    Promise.all([that.getOrganizeList(), that.getUserOrganize()]).then(result => {
       if(result[0]){
        //
        that.organize.orgID = result[1] ||  that.organizeList[0].orgID
        let node = that.$refs.orgtree.getNode(that.organize.orgID);
        if(node){
          that.$refs.orgtree.setCurrentKey(that.organize.orgID);
          that.setOrganize(node);
        }
        // 
        that.getCategoryOfOrganize(that.organize.orgID).then(()=>{
          that.imgLoading = false
        })
      }
    }).catch(error => {
      that.imgLoading = false
      that.$message.error(error)
    })
  },
  methods: {
    /**
      *  orgtree
      * @param {Object} data 
      * @param {Object} node  
      */
    selectTreeClick(data,node){
      let that = this;
      this.processName = ''; // 
      this.tooltiprocessName = '';
      that.$refs.selectTree.visible = false
      that.organize = data;
      that.setOrganize(node)
      that.imgLoading = true
       // 
      that.getCategoryOfOrganize(data.orgID).then(()=>{
        that.imgLoading = false
      })
      //
      that.cities = []
      that.checkAll = false          //
      that.isIndeterminate = false   //checkbox
    },
    /**
      *    
      * @param {Object} node 
      */
    setOrganize(node){
      this.activeOrganizeName= [];
      this.getOrgPrarentNode(node); // 
      this.organizeName = this.activeOrganizeName.reverse().join('/');
      this.organizeProcessName = this.activeOrganizeName.join('>')+'>';
    },
    /**
      * 
      * @returns {Promise}
      */
    getUserOrganize() {
      let that = this
      return new Promise((resolve, reject) => {
        that.$axios.post("/api/getorg", {}).then(res => {
            if(res.data.getOrgList.length >0){
              let data = res.data.getOrgList[0].orgid
              resolve(data)
            }else{
              resolve(null)
            }
          }).catch(err => {
            reject(`!ERROR MESSAGE:${err}`)
          })
      })
    },
    /**
      * 
      * @returns {Promise}
      */
    getOrganizeList() {
      let that = this
      return new Promise((resolve, reject) => {
        that.$axios.post("/api/companytree/page", {}).then(res => {
          if (res.data.companyTreeList.length > 0) {
            that.organizeList = res.data.companyTreeList
            resolve(true);
          }else{
            resolve(false);
          }
        }).catch(err => {
          reject(`!ERROR MESSAGE:${err}`)
        })
      })
    },
    /**
      * 
      * @param {String} orgID Id
      */
    getCategoryOfOrganize(orgID) {
      let that = this
      let obj = {
        orgId: orgID, // id
        isUse: 1, // 
        name: that.treeSearch.trim(), // 
        isHide: 1, // k2
        isDailog: "1"
      };
      return new Promise((resolve, reject) => {
        that.$axios.post("/api/processCategory", obj).then(res => {
          if (res.data.result.length == 0) {
            // that.$message.success("")
            that.treeList = []
          } else {
            let data = util.toTree(res.data.result,'id','pid','Child');
            // pid!=0
            for (let i = 0; i < data.length; i++) {
              if (data[i].pid != 0) {
                data.splice(i, 1);
                i--;
              }
            }
            that.treeList = data
          }
          resolve(true)
        }).catch(err => {
          reject(`!ERROR MESSAGE:${err}`)
        })
      })
    },
    /**
      *   
      * @param {Object} node 
      */
    getOrgPrarentNode(node) {
      if (node.data.orgName != undefined) {
        this.activeOrganizeName.push(node.data.orgName);
      }
      if (node.parent != undefined) {
        this.getOrgPrarentNode(node.parent);
      }
    },
    //
    enterSearch(){
      var that = this;
      that.defaultKeys = [];
      that.debouncedSearchTree(); //
    },
    // 
    searchTree() {
      var that = this;
       that.defaultKeys = [];
      //
      if(that.treeSearch.trim() == ""){
        //
        that.imgLoading = true
        that.getCategoryOfOrganize(that.organize.orgID).then(()=>{
          that.imgLoading = false
        })
        return;
      }
      //
      if(that.organize.orgID==''){
          that.$message.warning('');
          return;
      }
      // tree 
      that.searchProcessCategory();
    },
    // tree 
    searchProcessCategory(){
      var that = this;
      // tree 
      that.treeList.splice(0, that.treeList.length);
      // 
      that.cities.splice(0, that.cities.length);
      // 
      that.checkAll = false
      that.isIndeterminate = false
      var obj = {
        isCategory: "",         //  1  0
        isDailog: 1,            //
        isHide: 1,              // k2
        isUse: 1,               // 
        name: that.treeSearch.trim(),  // 
        id: "",                 // id
        orgId: that.organize.orgID  // 
      };
       that.imgLoading = true
      that.$axios.post("/api/processCategoryTree", obj).then(res => {
        if (res.data.result.length == 0) {
          // that.$message.success("")
          that.treeList = []
        } else {
          let data = util.toTree(res.data.result,'id','pid','Child')
          that.treeList = data
          that.getdefaultKeys(data)
        }
         that.imgLoading = false
      }).catch(err => {
         that.imgLoading = false
        that.$message.error(`${err}`);
      });
    },
    //  
    getProcess(node){
      if(!node){return;}
      var that = this;
      let processIds = that.getSelectProcessId(that.selectedCities);
      var obj = {
        isCategory: "0",      //  1  0
        isDailog: 1,          //
        isHide: 1,            // k2
        isUse: 1,             // 
        name: "",             // 
        id: node.data.id,     // id
        orgId: that.organize.orgID     // id
      };
      that.imgLoading = true
      that.$axios.post("/api/processCategoryTree", obj).then(res => {
        //
        that.displayProcess(res.data.result, processIds);
        that.imgLoading = false
      }).catch(err => {
        that.imgLoading = false
        that.$message.error(`${err}`);
      });
    },
    /**
     * tree 
     */
    handleNodeClick(data, node) {
      var that = this;
      //   
      if(that.treeSearch.trim().length == 0){
        //
         that.debouncedGetProcess(node);
      }else if(that.treeSearch.trim().length > 0){
         //  
        let processIds = that.getSelectProcessId(that.selectedCities);
        let list = [];
        //
        if(data.Child){
          list = data.Child;
        }else{
          list.push(data);
        }
        that.displayProcess(list, processIds);
      }

      that.selectOrgs = [];
      that.getPrarentNode(node);
      that.disposeOrganName(data.name);
    },
    //key
    getdefaultKeys(list){
       for (let it of list) {
         if(it.hasOwnProperty('Child') &&it.Child.length > 0){
            this.defaultKeys.push(it.id);
            this. getdefaultKeys(it.Child);
         }
       }
    },

    //
    displayProcess(list, processIds) {
      var that = this;
      that.cities.splice(0, that.cities.length);
      that.checkedCities = []
      // 
      that.checkAll = false
      that.isIndeterminate = false
      for (let item of list) {
        // 
        if (item.processId && item.processId !== "0" && item.processId !== null ) {
          if (processIds.indexOf(item.processId) > -1) {
            that.checkedCities.push(item);
          }
          that.cities.push(item);
        }
      }
       var citiesIds = that.getId(that.cities);
      //  
      var checkedCount = 0
      for(let item of that.checkedCities){
        if (citiesIds.indexOf(item.id) !== -1) {
          checkedCount++
        }
      }
      that.checkAll = checkedCount > 0 && checkedCount === that.cities.length;
      that.isIndeterminate =
        that.checkedCities.length > 0 &&
        that.checkedCities.length < that.cities.length;
    },
    //  
    dblclickCheckedCitie(item) {
      let that = this;
      that.clickDepartment(item);
      that.$emit("sureSubmit", {
        type: that.componentParam.componentType,
        list: that.selectedCities
      });
    },
    /**
     *  
     */
    clickDepartment(item) {
      let that = this;
      this.selectedCities.splice(0, this.selectedCities.length);
      this.selectedCities.push(item);
      this.checkedCities.splice(0, this.checkedCities.length);
      this.checkedCities.push(item);
      //tree
      let node = that.$refs.processTree.getNode(item.pid);
      that.selectOrgs = [];
      that.getPrarentNode(node);
      item.tooltiprocessName = that.disposeOrganName(item.name);
    },

    // /
    handleCheckAllChange(val) {
      this.checkedCities = [];
      // processId
      let processId = this.getSelectProcessId(this.selectedCities);
      if (val) {
        for (let it of this.cities) {
          if (processId.indexOf(it.processId) === -1) {
            this.selectedCities.push(it);
          }
          this.checkedCities.push(it)
        }
        this.checkAll = true;
        this.isIndeterminate = false;
      } else {
        for (let it of this.cities) {
          if (processId.indexOf(it.processId) > -1) {
            let index = -1;
            for (let item in this.selectedCities) {
              if (this.selectedCities[item].processId == it.processId) {
                index = item;
              }
            }
            if (index != -1) {
              this.selectedCities.splice(index, 1);
            }
          }
        }
        this.checkAll = false;
        this.isIndeterminate = false;
      }
    },

    /**
     * , 
     */
    handleCheckedCitiesChange(value) {
      //1  
      this.isIndeterminate = false
      let selectList = [];
      for (let item of this.selectedCities) {
        selectList.push(item);
      }
      this.selectedCities.splice(0, this.selectedCities.length);
      let citiesIds = this.getId(this.cities);
      let data = []
      for (let it in selectList) {
        if (citiesIds.indexOf(selectList[it].id) == -1) {
          data.push(selectList[it]);
        }
      }
      this.selectedCities = data;

      //2
      let item = [];
      for (let it of value) {
        item.tooltiprocessName = this.disposeOrganName(it.name);
      }

      let processId = this.getSelectProcessId(this.selectedCities);
      for (let it of value) {
        if (processId.indexOf(it.processId) === -1) {
          this.selectedCities.push(it);
        }
      }
       //  
      var checkedCount = 0
      for(let item of value){
        if (citiesIds.indexOf(item.id) !== -1) {
          checkedCount++
        }
      }
      this.checkedCities = value;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length;
    },

    /**
     * 
     * style by pcCandidates.scss
     */

    pullBtnHeightChange() {
      this.isActive = !this.isActive;
    },

    // 
    sureSubmit() {
      this.$emit("sureSubmit", {
        type: this.componentParam.componentType,
        list: this.selectedCities
      });
    },
    /**
     * ProcessId
     */
    getSelectProcessId(list) {
      let processId = [];
      for (let citie of list) {
        processId.push(citie.processId);
      }
      return processId;
    },
    //Id
    getId(list) {
      let id = [];
      for (let citie of list) {
        id.push(citie.id);
      }
      return id;
    },
    // 
    pullBtnHeightChange() {
      this.isActive = !this.isActive;
    },
    //
    close() {
      this.$emit("hideComponent");
    },
    // 
    deleteDept(index) {
      // 
      this.checkAll = false
      this.isIndeterminate = false
      for (let i in this.checkedCities) {
        if (this.checkedCities[i].id == this.selectedCities[index].id) {
          this.checkedCities.splice(i, 1);
        }
      }
      this.selectedCities.splice(index, 1);
      this.checkAll = this.checkedCities.length === this.cities.length && this.cities.length > 0;
      this.isIndeterminate =
        this.checkedCities.length > 0 &&
        this.checkedCities.length < this.cities.length;
    },
    //  
    clearDept() {
      this.checkedCities.splice(0, this.checkedCities.length);
      this.selectedCities.splice(0, this.selectedCities.length);
      this.checkAll = false;
      this.isIndeterminate = false;
    },

    /**
     * 
     *  return organName
     */
    disposeOrganName(orgNames) {
      var organName = "";
      //
      for (let it of this.selectOrgs) {
        organName += `${it.text} > `;
      }
      organName = organName.trim();
      organName = organName.substr(0, organName.length - 1);
      this.tooltiprocessName = organName;
      if (organName.length > 30) {
        this.processName = organName.substr(0, 30) + "...";
      } else {
        this.processName = organName;
      }
      return organName;
    },
    //
    getPrarentNode(pNode) {
       if (!pNode){ return}
       if (pNode.data && pNode.data.name != undefined) {
        this.selectOrgs.unshift({
          text: `${pNode.data.name}`,
          petId: `${pNode.data.id}`,
          PID: `${pNode.data.pid}`
        });
      }
      if (pNode.parent != undefined) {
        this.getPrarentNode(pNode.parent);
      }
    }
  }
};
</script>


