      .top {
        display: flex;
        justify-content: space-between;
        font-size: 16px;
        color: #fff;
        height: 50px;
        line-height: 50px;
        background: #38acff;
        background: -webkit-linear-gradient(right, #3dd7fe , #3195f8); /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(left, #3dd7fe , #3195f8); /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(left, #3dd7fe , #3195f8); /* Firefox 3.6 - 15 */
        background: linear-gradient(to left,  #3dd7fe , #3195f8); /*  */
        padding: 0 20px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
      }
    }
  }

  .clearfix:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }

  #pane-1 .el-breadcrumb {
    font-size: 12px;
    padding-top: 16px;
  }

  #pane-0 .el-breadcrumb {
    font-size: 12px;
    padding-top: 16px;
  }

  .candidates-recently .el-tabs {
    position: relative;
  }

  .candidates-recently .title {
    height: 40px;
    line-height: 40px;
    padding-left: 30px;
    color: #4e4e4e;
